import React from 'react';
import Search from './components/Search'
import List from './components/List'
import Google from './Api/Api'
import Search1 from './components/search1'


 class App extends React.Component{
  state = { resp: []}

  componentDidMount(){
    this.onTermSubmit('Physics')
}

  onTermSubmit = async (term) =>{

   const response = await Google({
    params:{
        key: 'AIzaSyBIW8TaGw50GbDECVzSeuRXHTjFcIBONZQ',
        cx: `002034738127848283789:jojxdafdtl1`,
        q: ` ${term}`,
        num:10,
        safe:'active'     
        
      }
    })
    this.setState({resp:response.data.items});
    //console.log(this.state.resp[0])
  };
  


  render() {
    return (
        <div className="ui container">
          <Search onFormSubmit={this.onTermSubmit}/>
          <div className="ui grid">
              <div className="ui row">
                  <div className ="eleven wide column">
                      <List result={this.state.resp}/>
                  </div>
                  <div className ="five wide column">
                      <Search1 result1={this.state.resp[0]}/>
                  </div>
              </div>
          </div>
        </div>
    );
  }
}
//

export default App;
